<template>
<div v-bind:style="{backgroundColor: background}" style="padding-bottom: 50px; width: 100%;" class="n">
    <div class="content">
        <div class="imageContainer">
            <img class="image" v-if="isImage" v-bind:src="imagePath" alt="">        
        </div>
            <p v-bind:class="{title:!isImage, titleb: isImage}" :style="{color: titleColor}">{{title}}</p>
            <div v-if="!isImage" class="spacing"></div>
            <div class="text">
                <slot></slot>
            </div>
            <div class="block"><transition name="slide-fade"><img class="icon" v-if="isIcon" v-bind:src="iconPath" alt=""></transition></div>
    </div>
</div>
</template>
<script>
    export default{
        props: ["bg", "icon", "title", "image", "tc"],
        data(){
            return{
                iconPath: "",
                background: "",
                titleColor: "",
                isImage: false,
                imagePath: "",
                isIcon: false
            }
        },
        methods: {
        },
        mounted(){
            this.title = this.title.toUpperCase()
            if(this.image !== undefined){
                console.log(this.image)
                this.isImage = true;
                this.imagePath = "static/"+this.image+".svg"
            }

            if(this.icon !== undefined){
                this.isIcon = true;
                this.iconPath = "static/"+this.icon+".svg"
            }

            switch(this.bg){
                case "green":
                    this.background = "#51F5A0"
            }

            switch(this.tc){
                case "green":
                    this.titleColor = "#51F5A0"
            }
            
        }
    }
</script>
<style>
.title{
    font-size: 100px;
    text-align: center;
    margin: 50px;
}

.titleb{
    font-size:100px;
    text-align: center;
    margin-top: -100px; 
}

.block{

    position: relative;
    bottom: -92px;
}
.icon{
    display: block;
    margin: auto;
}

.content{
    width: 1024px;
    margin: auto;
}

.imageContainer{
    top: -150px;
    position: relative;
}

.image{
    display: block;
    margin: auto;
}
.spacing{
    height: 80px;
    width: 100%;
}

.text{
    font-size: 48px;
    max-width: 700px;
    margin: auto;
}
</style>


